import React, { useState, useEffect } from "react";
import axios from "axios";

function SystemList({ onSystemSelect }) {
  const [systems, setSystems] = useState([]);
  const [selectedSystem, setSelectedSystem] = useState({
    id: null,
    color: null,
  });

  // Define colorMapping if needed
  const colorMapping = {}; // Define your color mapping here

  useEffect(() => {
    axios
      .get("https://smithtech.io/react/systems")
      .then((response) => {
        setSystems(response.data);
      })
      .catch((error) => {
        console.error("Error fetching systems:", error);
      });
  }, []);

  const handleSystemSelect = (systemId) => {
    const color = colorMapping[systemId] || null; // Fallback to null if not found
    setSelectedSystem({ id: systemId, color: color });
    if (onSystemSelect) {
      onSystemSelect(systemId, color); // Call onSystemSelect if it's provided
    }
  };

  return (
    <div className="system-list-container bg-gray-900 p-4 rounded-lg shadow">
      <h2 className="system-list-label text-xl text-white font-bold mb-3">
        Select a Hospital System
      </h2>
      <div className="system-buttons">
        {systems.map((system) => (
          <button
            key={system.SystemID}
            onClick={() => handleSystemSelect(system.SystemID)}
            className={`system-button m-1 ${
              selectedSystem.id === system.SystemID
                ? "bg-[#00e3d2] text-white"
                : "bg-gray-700 text-gray-300 hover:bg-gray-600"
            }`}
          >
            {system.SystemName}
          </button>
        ))}
      </div>
    </div>
  );
}

export default SystemList;
