import React from "react";
import axios from "axios";
import { useChargeSheet } from "../../contexts/ChargeSheetContext";
import "./TableRow.css";
function TableRow({ charge, selectedColumns, rowIndex }) {
  const { selectedChargeSheet } = useChargeSheet();
  const { UserChargeSheetID, UserID } = selectedChargeSheet || {};

  const handleAddClick = async () => {
    if (!UserChargeSheetID || !UserID) {
      alert("UserChargeSheetID or UserID not available in context.");
      return;
    }

    const data = {
      charge_sheet_id: UserChargeSheetID,
      user_id: UserID,
      location_id: charge.LocationID,
      system_id: charge.SystemID,
      charge_id: charge.ChargeID,
    };

    try {
      const response = await axios.post(
        "https://smithtech.io/react/chargesheet/add_charge",
        data
      );

      if (response.status === 200) {
        alert("Charge added successfully");
      } else {
        alert(`Failed to add charge: ${response.data.error}`);
      }
    } catch (error) {
      alert(`Error adding charge: ${error}`);
    }
  };

  return (
    <tr className="border-b border-gray-700 hover:bg-gray-700">
      <td className="px-4 py-2">
        <button
          onClick={handleAddClick}
          className="text-sm bg-[#00e3d2] text-white py-1 px-2 rounded hover:bg-[#00c2c0] transition duration-300"
        >
          Add
        </button>
      </td>
      {selectedColumns.map((column, columnIndex) => (
        <td
          key={`charge-${charge.ChargeID || rowIndex}-${column}-${columnIndex}`}
          className="px-4 py-2"
        >
          {charge[column]}
        </td>
      ))}
    </tr>
  );
}

export default TableRow;
