import React from "react";
import { BrowserRouter as Router, Routes, Route } from "react-router-dom";
import Header from "./Components/Header/Header";
import SignupPage from "./Components/SignupPage/SignupPage";
import ChargeMasterPage from "./Components/ChargeMasterPage/ChargeMasterPage";
import Dashboard from "./Components/Dashboard/Dashboard";
import LoginPage from "./Components/LoginPage/LoginPage";
import ProtectedRoute from "./Components/ProtectedRoute/ProtectedRoute";
import EligibilityPage from "./Components/EligibilityPage/EligibilityPage";
import SingleChargeSearch from "./Components/SingleChargePage/SingleCharge";
import { UserProvider } from "./Components/contexts/UserContext";
import { ChargeSheetProvider } from "./Components/contexts/ChargeSheetContext"; // Import ChargeSheetProvider
import "./tailwind.css";
import LandingPage from "./Components/LandingPage/LandingPage";
import ChargeSheetPage from "./Components/ChargeSheetPage/ChargeSheetPage";
import Footer from "./Components/Footer/Footer";

function App() {
  return (
    <UserProvider>
      <ChargeSheetProvider>
        {" "}
        {/* Wrap your Router with ChargeSheetProvider */}
        <Router>
          <Header />
          <Routes>
            <Route path="/" element={<LandingPage />} />
            <Route path="/login" element={<LoginPage />} />
            <Route path="/signup" element={<SignupPage />} />
            <Route
              path="/chargemaster"
              element={
                <ProtectedRoute>
                  <ChargeMasterPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/dashboard"
              element={
                <ProtectedRoute>
                  <Dashboard />
                </ProtectedRoute>
              }
            />
            <Route
              path="/eligibility"
              element={
                <ProtectedRoute>
                  <EligibilityPage />
                </ProtectedRoute>
              }
            />
            <Route
              path="/singlechargesearch"
              element={
                <ProtectedRoute>
                  <SingleChargeSearch />
                </ProtectedRoute>
              }
            />
            <Route
              path="/chargesheet"
              element={
                <ProtectedRoute>
                  <ChargeSheetPage />
                </ProtectedRoute>
              }
            />
            {/* other routes */}
          </Routes>
          <Footer /> {/* Include Footer component */}
        </Router>
      </ChargeSheetProvider>
    </UserProvider>
  );
}

export default App;
