import React, { useState, useEffect } from "react";
import axios from "axios";

function LocationList({ selectedSystem, onLocationSelect }) {
  const [locations, setLocations] = useState([]);
  const [isLoading, setLoading] = useState(false);
  const [selectedLocationId, setSelectedLocationId] = useState(null);

  useEffect(() => {
    if (selectedSystem.id !== null) {
      setLoading(true);
      axios
        .get(`https://smithtech.io/react/locations/${selectedSystem.id}`)
        .then((response) => {
          setLocations(response.data);
          setLoading(false);
        })
        .catch((error) => {
          console.error("Error fetching locations:", error);
          setLoading(false);
        });
    } else {
      setLocations([]);
    }
  }, [selectedSystem.id]);

  const handleLocationClick = (locationId) => {
    onLocationSelect(locationId);
    setSelectedLocationId(locationId);
  };

  return (
    <div className="location-list-container bg-gray-900 p-4 rounded-lg shadow text-white">
      <h2 className="location-list-label text-xl font-bold mb-3">
        Select a Location
      </h2>
      <div className="location-buttons">
        {isLoading ? (
          <p>Loading locations...</p>
        ) : locations.length > 0 ? (
          locations.map((location) => (
            <button
              key={location.LocationID}
              onClick={() => handleLocationClick(location.LocationID)}
              className={`location-button m-1 ${
                selectedLocationId === location.LocationID
                  ? "bg-[#00e3d2] text-white"
                  : "bg-gray-700 text-gray-300 hover:bg-gray-600"
              }`}
            >
              {location.LocationName}
            </button>
          ))
        ) : (
          <p>No locations available for the selected system.</p>
        )}
      </div>
    </div>
  );
}

export default LocationList;
