import React, { useState } from "react";
import BillingSearch from "./BillingSearch";
import DescriptionSearch from "./DescriptionSearch";
import ResultsChargeSheet from "./ResultsChargeSheet";

function SingleChargeSearch() {
  const [results, setResults] = useState([]);
  const [activeTab, setActiveTab] = useState("description"); // 'description' or 'billing'

  const handleResults = (newResults) => {
    setResults(newResults);
  };

  return (
    // Set the page background to teal
    <div className="single-charge-search-container bg-[#00e3d2] min-h-screen p-4">
      {/* Content container with a dark background */}
      <div className="max-w-7xl mx-auto bg-gray-900 rounded-lg shadow-lg p-6 text-white">
        <header className="text-3xl font-bold mb-6">Charge Search</header>
        <div className="tab-buttons-container flex justify-center space-x-4 mb-4">
          <button
            className={`tab-button p-2 rounded ${
              activeTab === "description"
                ? "bg-[#00c2c0] text-white"
                : "bg-gray-700 text-white"
            }`}
            onClick={() => setActiveTab("description")}
          >
            Description
          </button>
          <button
            className={`tab-button p-2 rounded ${
              activeTab === "billing"
                ? "bg-[#00c2c0] text-white"
                : "bg-gray-700 text-white"
            }`}
            onClick={() => setActiveTab("billing")}
          >
            Billing Code
          </button>
        </div>
        <div className="search-content">
          {activeTab === "description" ? (
            <DescriptionSearch onResults={handleResults} />
          ) : (
            <BillingSearch onResults={handleResults} />
          )}
        </div>
        <ResultsChargeSheet results={results} />
      </div>
    </div>
  );
}

export default SingleChargeSearch;
