import React, { useState, useEffect } from "react";
import axios from "axios";
import ChargeSheetDetailsItems from "./ChargeSheetDetailsItems";
import ChargeSheetDelete from "./ChargeSheetDelete";
import ChargeSheetSelectActive from "./ChargeSheetSelectActive";

const ChargeSheetDetails = ({ userId, chargeSheetId, onDeleteSuccess }) => {
  const [details, setDetails] = useState([]);
  const [chargeSheetNameDefault, setChargeSheetNameDefault] = useState("");
  const [error, setError] = useState("");
  const [showItems, setShowItems] = useState(false);

  useEffect(() => {
    const fetchDetails = async () => {
      try {
        const response = await axios.get(
          `https://smithtech.io/react/chargesheet/details/${userId}/${chargeSheetId}`
        );
        console.log("API Response:", response.data); // Log the response
        setDetails(response.data.details || []);
        setChargeSheetNameDefault(
          response.data.chargeSheetNameDefault || "Unknown"
        );
      } catch (error) {
        console.error("Error fetching charge sheet details", error);
        setError("Could not fetch charge sheet details.");
        if (error.response && error.response.status === 500) {
          setDetails([]);
        }
      }
    };

    if (userId && chargeSheetId) {
      fetchDetails();
    }
  }, [userId, chargeSheetId]);

  const toggleItems = () => {
    setShowItems(!showItems);
  };

  const uniqueLocations = new Set(details.map((detail) => detail.LocationID));
  const summary = {
    numberOfCharges: details.length,
    numberOfLocations: uniqueLocations.size,
  };

  const activeChargeSheetData = {
    UserChargeSheetID: chargeSheetId,
    UserID: userId,
    ChargeSheetNameDefault: chargeSheetNameDefault, // Use the state here
  };

  return (
    <div className="space-y-4 bg-gray-900 min-h-screen p-4 text-white">
      <div className="rounded-lg shadow-lg p-6">
        <h2 className="text-3xl font-semibold mb-4">Charge Sheet Details</h2>
        <div className="mb-6">
          <h4 className="text-xl font-bold">Summary</h4>
          <p>Number of Charges: {summary.numberOfCharges}</p>
          <p>Number of Locations: {summary.numberOfLocations}</p>
        </div>
        <ChargeSheetDelete
          userChargeSheetId={chargeSheetId}
          onDeleteSuccess={onDeleteSuccess}
        />
        {error && <p className="text-red-600">{error}</p>}

        <ChargeSheetSelectActive
          chargeSheet={activeChargeSheetData}
          chargeSheetNameDefault={chargeSheetNameDefault} // Pass the chargeSheetNameDefault
          onUpdateActiveChargeSheet={setChargeSheetNameDefault}
        />

        {details.length > 0 && (
          <button
            onClick={toggleItems}
            className="mb-6 p-2 w-full text-lg rounded bg-teal-500 hover:bg-teal-600 transition duration-300"
          >
            {showItems ? "Hide Items" : "Show Items"}
          </button>
        )}
        {showItems && (
          <div className="space-y-2">
            <h4 className="text-xl font-bold">Charge Sheet Details Items</h4>
            {details.map((detail) => (
              <ChargeSheetDetailsItems
                key={detail.ChargeSheetDetailsID}
                detail={detail}
              />
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default ChargeSheetDetails;
