import React, { useState } from "react";
import { Link } from "react-router-dom";

const SignupComponent = () => {
  const [firstName, setFirstName] = useState("");
  const [lastName, setLastName] = useState("");
  const [companyName, setCompanyName] = useState("");
  const [phone, setPhone] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [message, setMessage] = useState(""); // For success or error message
  const [isError, setIsError] = useState(false); // To differentiate between success and error

  const handleFirstNameChange = (e) => setFirstName(e.target.value);
  const handleLastNameChange = (e) => setLastName(e.target.value);
  const handleCompanyNameChange = (e) => setCompanyName(e.target.value);
  const handlePhoneChange = (e) => setPhone(e.target.value);
  const handleEmailChange = (e) => setEmail(e.target.value);
  const handlePasswordChange = (e) => setPassword(e.target.value);

  const handleSubmit = async () => {
    const userData = {
      first_name: firstName,
      last_name: lastName,
      email: email,
      phone_number: phone,
      company: companyName,
      user_type_id: 1,
      password: password,
    };

    try {
      const response = await fetch("https://smithtech.io/auth/register", {
        // Update the URL to match your endpoint
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(userData),
      });

      const result = await response.json();
      if (response.ok) {
        setMessage("User registered successfully");
        setIsError(false);
        // Optionally, clear the form or redirect the user
      } else {
        setMessage(result.message || "Registration failed");
        setIsError(true);
      }
    } catch (error) {
      setMessage("Registration failed: " + error.message);
      setIsError(true);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-[#00e3d2]">
      <div className="login-container max-w-md mx-auto p-6 bg-gray-800 text-white rounded-lg shadow-lg">
        <h2 className="text-3xl font-bold mb-4">Sign Up</h2>
        {message && (
          <div
            className={`mb-4 ${isError ? "text-red-500" : "text-green-500"}`}
          >
            {message}
          </div>
        )}
        <form>
          <div className="mb-4">
            <label className="block mb-2">First Name:</label>
            <input
              type="text"
              placeholder="Enter your First Name"
              value={firstName}
              onChange={handleFirstNameChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
            />
          </div>
          <div className="mb-4">
            <label className="block mb-2">Last Name:</label>
            <input
              type="text"
              placeholder="Enter your Last Name"
              value={lastName}
              onChange={handleLastNameChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
            />
          </div>
          <div className="mb-4">
            <label className="block mb-2">Company/Organization Name:</label>
            <input
              type="text"
              placeholder="Enter your Company/Organization Name"
              value={companyName}
              onChange={handleCompanyNameChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
            />
          </div>
          <div className="mb-4">
            <label className="block mb-2">Phone:</label>
            <input
              type="tel"
              placeholder="Enter your Phone Number"
              value={phone}
              onChange={handlePhoneChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
            />
          </div>
          <div className="mb-4">
            <label className="block mb-2">Email Address:</label>
            <input
              type="email"
              placeholder="Enter your Email Address"
              value={email}
              onChange={handleEmailChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
            />
          </div>
          <div className="mb-6">
            <label className="block mb-2">Password:</label>
            <input
              type="password"
              placeholder="Enter your Password"
              value={password}
              onChange={handlePasswordChange}
              className="w-full p-2 rounded bg-gray-700 text-white"
              autoComplete="current-password"
            />
          </div>
          <button
            type="button"
            onClick={handleSubmit}
            className="w-full bg-[#00c2c0] text-white py-2 px-4 rounded hover:bg-[#00e3d2] font-bold"
          >
            Sign Up
          </button>
        </form>
        <p className="mt-4">
          Already have an account?{" "}
          <Link to="/login" className="text-[#00c2c0] hover:text-[#00e3d2]">
            Login
          </Link>
        </p>
      </div>
    </div>
  );
};

export default SignupComponent;
