import React, { useState, useEffect } from "react";
import axios from "axios";
import Select from "react-select";
import EligibleLocationList from "./EligibleLocationList";
import "./InsurancePlanList.css";
function InsurancePlanList({ carrierId }) {
  const [insurancePlans, setInsurancePlans] = useState([]);
  const [selectedPlans, setSelectedPlans] = useState([]);
  const [isLoading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Custom styling for Select component
  const customSelectStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: "rgba(31, 41, 55)", // Dark background
      color: "white", // Light text
    }),
    option: (styles, { isFocused, isSelected }) => ({
      ...styles,
      backgroundColor: isSelected
        ? "#00e3d2"
        : isFocused
        ? "rgba(55, 65, 81)"
        : "rgba(31, 41, 55)",
      color: "white",
      cursor: "pointer",
    }),
    // Add other styles as needed
  };

  useEffect(() => {
    if (carrierId) {
      setLoading(true);
      axios
        .get(
          `https://smithtech.io/react/eligibility/insurance-plans/${carrierId}`
        )
        .then((response) => {
          const transformedPlans = response.data.map((plan) => ({
            value: plan.PlanID,
            label: plan.PlanName,
          }));
          setInsurancePlans(transformedPlans);
          setLoading(false);
        })
        .catch((error) => {
          console.error("Error fetching insurance plans:", error);
          setError(error.message);
          setLoading(false);
        });
    }
  }, [carrierId]);

  const handlePlanSelect = (selectedOptions) => {
    setSelectedPlans(selectedOptions || []);
  };

  if (isLoading) {
    return <div className="text-white">Loading insurance plans...</div>;
  }

  if (error) {
    return <div className="text-red-500">{error}</div>;
  }

  return (
    <div className="insurance-plan-list bg-gray-900 p-4 rounded-lg shadow text-white">
      <Select
        isMulti
        name="plans"
        options={insurancePlans}
        value={selectedPlans}
        onChange={handlePlanSelect}
        styles={customSelectStyles}
        classNamePrefix="react-select"
      />
      {selectedPlans.map((plan) => (
        <div key={plan.value} className="plan-container my-2">
          <h4 className="plan-title font-bold">{plan.label}</h4>
          <EligibleLocationList planId={plan.value} />
        </div>
      ))}
    </div>
  );
}

export default InsurancePlanList;
