import React, { useState, useEffect } from "react";
import axios from "axios";

const ChargeSheetDetailsItems = ({ detail }) => {
  const [chargeDetails, setChargeDetails] = useState({});
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchChargeDetails = async () => {
      try {
        const response = await axios.post(
          "https://smithtech.io/react/chargesheet/get_charge_info",
          {
            system_id: detail.SystemID,
            location_id: detail.LocationID,
            charge_id: detail.ChargeID,
          }
        );

        setChargeDetails(response.data.charge_info);
      } catch (error) {
        console.error("Error fetching charge details:", error);
        // Handle the error appropriately
      } finally {
        setLoading(false);
      }
    };

    fetchChargeDetails();
  }, [detail]);

  const renderValue = (key, value) => {
    // Check if key is "LocationName" or "ServiceDescription"
    const isSpecialKey = key === "LocationName" || key === "ServiceDescription";

    // Apply a different style for these keys
    const liClassName = isSpecialKey ? "special-item" : "";

    if (typeof value === "object" && value !== null) {
      // Extract entries and filter out unwanted ones
      const entries = Object.entries(value).filter(
        ([key, val]) =>
          val !== null &&
          val !== "NA" &&
          val !== undefined &&
          !key.endsWith("ID")
      );

      // Sort the priority entries to the front
      const sortedEntries = entries.sort((a, b) => {
        const priority = [
          "LocationName",
          "ServiceDescription",
          "BillingCode",
          "GrossCharge",
          "DiscountedCashPrice",
        ];
        const aPriority = priority.indexOf(a[0]);
        const bPriority = priority.indexOf(b[0]);
        if (aPriority !== -1 && bPriority !== -1) {
          return aPriority - bPriority; // Sort by priority order
        } else if (aPriority !== -1) {
          return -1; // a has priority, sort it to the front
        } else if (bPriority !== -1) {
          return 1; // b has priority, sort it to the front
        }
        return a[0].localeCompare(b[0]); // Alphabetical sort for non-priority items
      });

      // Return a list with the sorted entries
      return (
        <ul>
          {sortedEntries.map(([key, val]) => (
            <li key={key} className={`mb-1 ${liClassName}`}>
              {`${key}: ${val}`}
            </li>
          ))}
        </ul>
      );
    }
    // For non-object values
    return <span className={liClassName}>{value.toString()}</span>;
  };

  const sortedAndFilteredEntries = (details) => {
    const priorityOrder = [
      "LocationName",
      "ServiceDescription",
      "BillingCode",
      "GrossCharge",
      "DiscountedCashPrice",
    ];

    // Filter and sort the priority keys first
    const priorityData = priorityOrder
      .map((key) => [key, details[key]])
      .filter(
        ([key, value]) =>
          value !== null && value !== "NA" && value !== undefined
      );

    // Get the remaining data, excluding keys ending with 'ID' and priority keys
    const remainingData = Object.entries(details)
      .filter(
        ([key, value]) =>
          !key.endsWith("ID") &&
          !priorityOrder.includes(key) &&
          value !== null &&
          value !== "NA" &&
          value !== undefined
      )
      .sort((a, b) => a[0].localeCompare(b[0])); // Sort alphabetically by key

    // Combine the arrays, with the priority data first
    return [...priorityData, ...remainingData];
  };

  const excludedKeys = ["SystemID", "LocationID", "ChargeID"];

  if (loading) {
    return <div>Loading charge details...</div>;
  }

  return (
    <div className="mb-4 p-4 border border-gray-600 rounded">
      <ul>
        {sortedAndFilteredEntries(chargeDetails)
          .filter(([key]) => !excludedKeys.includes(key))
          .map(([key, value]) => (
            <li key={key} className="mb-1">
              {renderValue(key, value)}
            </li>
          ))}
      </ul>
    </div>
  );
};

export default ChargeSheetDetailsItems;
