import React, { useState, useEffect } from "react";
import axios from "axios";
import "./LocationCard.css";

function LocationCard({ locationId }) {
  const [locationDetails, setLocationDetails] = useState(null);
  const [isExpanded, setIsExpanded] = useState(false);
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);

  useEffect(() => {
    if (locationId) {
      setIsLoading(true);
      axios
        .get(
          `https://smithtech.io/react/eligibility/location-details/${locationId}`
        )
        .then((response) => {
          setLocationDetails(response.data);
          setIsLoading(false);
        })
        .catch((error) => {
          console.error("Error fetching location details:", error);
          setError(error.message);
          setIsLoading(false);
        });
    }
  }, [locationId]);

  const formatFullAddress = (details) => {
    return details
      ? `${details.Address}, ${details.City}, ${details.State} ${details.ZipCode}`
      : "";
  };

  const formatPhoneNumber = (phoneNumber) => {
    // Ensure phoneNumber is a string
    const phoneString = phoneNumber ? phoneNumber.toString() : "";
    return phoneString.length === 10
      ? `(${phoneString.substring(0, 3)})-${phoneString.substring(
          3,
          6
        )}-${phoneString.substring(6)}`
      : "";
  };

  const createGoogleMapsUrl = (details) => {
    return details
      ? `https://www.google.com/maps/place/${encodeURIComponent(
          `${details.Address}, ${details.City}, ${details.State} ${details.ZipCode}`
        )}`
      : "";
  };

  if (isLoading)
    return <div className="text-white">Loading location details...</div>;
  if (error) return <div className="text-red-500">Error: {error}</div>;

  return (
    <div
      className={`location-card bg-gray-900 p-4 rounded-lg shadow text-white ${
        isExpanded ? "expanded" : ""
      }`}
    >
      {locationDetails && (
        <>
          <div className="header-content">
            <h4 className="font-bold text-lg">
              {locationDetails.LocationName}
            </h4>
            <button
              onClick={() => setIsExpanded(!isExpanded)}
              className="toggle-details-button"
            >
              {isExpanded ? "Hide" : "Show"} Details
            </button>
          </div>

          {isExpanded && (
            <div className="location-details mt-4 text-sm">
              <p>
                <strong>Address:</strong> {formatFullAddress(locationDetails)}
              </p>
              {locationDetails.Phone && (
                <p>
                  <strong>Phone:</strong>
                  <a
                    href={`tel:${locationDetails.Phone}`}
                    className="hover:text-teal-300"
                  >
                    {formatPhoneNumber(locationDetails.Phone)}
                  </a>
                </p>
              )}
              {/* Insert additional details if needed */}
            </div>
          )}

          <div className="button-group">
            {locationDetails.BaseURL && (
              <button
                onClick={() => window.open(locationDetails.BaseURL, "_blank")}
                className="website-button"
              >
                Home Page
              </button>
            )}
            {locationDetails.EligURL && (
              <button
                onClick={() => window.open(locationDetails.EligURL, "_blank")}
                className="pricing-info-button"
              >
                Pricing Info
              </button>
            )}
            <button
              onClick={() =>
                window.open(createGoogleMapsUrl(locationDetails), "_blank")
              }
              className="google-maps-button"
            >
              Google Maps
            </button>
          </div>
        </>
      )}
    </div>
  );
}

export default LocationCard;
