import React from "react";
import { NavLink } from "react-router-dom";

const NavigationLinks = ({ isActive, user }) => {
  return (
    <ul className="flex space-x-4">
      {user ? (
        <>
          <li>
            <NavLink
              to="/chargemaster"
              className={
                isActive("/chargemaster") ? "text-white" : "text-black"
              }
            >
              ChargeMaster
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/eligibility"
              className={isActive("/eligibility") ? "text-white" : "text-black"}
            >
              EligibilityTool
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/singlechargesearch"
              className={
                isActive("/singlechargesearch") ? "text-white" : "text-black"
              }
            >
              Single Charge Search
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/chargesheet"
              className={isActive("/chargesheet") ? "text-white" : "text-black"}
            >
              Charge Sheet
            </NavLink>
          </li>
          <li>
            <a
              href="https://chat.openai.com/g/g-ImFds0gjv-chargemastergpt"
              target="_blank"
              rel="noopener noreferrer"
              className="text-black hover:text-white"
            >
              ChargeMasterGPT
            </a>
          </li>
        </>
      ) : (
        <>
          <li>
            <NavLink
              to="/login"
              className={isActive("/login") ? "text-white" : "text-black"}
            >
              Login
            </NavLink>
          </li>
          <li>
            <NavLink
              to="/signup"
              className={isActive("/signup") ? "text-white" : "text-black"}
            >
              Sign Up
            </NavLink>
          </li>
        </>
      )}
    </ul>
  );
};

export default NavigationLinks;
