import React, { useState } from "react";
import axios from "axios";

function BillingSearch({ onResults }) {
  const [billingCode, setBillingCode] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSearch = () => {
    const encodedBillingCode = encodeURIComponent(billingCode.trim());
    if (!encodedBillingCode) return;
    setLoading(true);
    axios
      .get(
        `https://smithtech.io/react/charges/billingcode/${encodedBillingCode}`
      )

      .then((response) => {
        onResults(response.data);
        console.log(response.data);
        setLoading(false);
      })
      .catch((error) => {
        setError(error.message);
        setLoading(false);
      });
  };

  return (
    <div className="billing-search-container max-w-lg mx-auto p-4">
      <input
        className="billing-search-input bg-gray-700 text-white rounded-lg shadow-sm px-4 py-3 w-full mb-4 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-transparent"
        type="text"
        value={billingCode}
        onChange={(e) => setBillingCode(e.target.value)}
        placeholder="Enter Billing Code"
      />
      <button
        className={`billing-search-button bg-teal-700 hover:bg-teal-600 text-white px-6 py-3 rounded-lg transition duration-200 ${
          !billingCode.trim() ? "opacity-50 cursor-not-allowed" : ""
        }`}
        onClick={handleSearch}
        disabled={!billingCode.trim()}
      >
        Search Billing Code
      </button>
      {isLoading && (
        <div className="loading-message text-white mt-3">Loading...</div>
      )}
      {error && (
        <div className="error-message text-red-500 mt-3">Error: {error}</div>
      )}
    </div>
  );
}

export default BillingSearch;
