import React, { useState } from "react";
import SystemList from "./SystemList/Systemlist";
import LocationList from "./LocationList/LocationList";
import LocationDetails from "./LocationDetails/LocationDetails";
import ChargeTableDisplay from "./ChargeTableDisplay/ChargeTableDisplay";
import "./ChargeMasterPage.css";

function ChargeMasterPage() {
  const [selectedSystem, setSelectedSystem] = useState({
    id: null,
    color: null,
  });
  const [selectedLocation, setSelectedLocation] = useState(null);
  const [selectedRows, setSelectedRows] = useState([]); // This holds the selected charges

  const handleSystemSelect = (systemId, systemColor) => {
    setSelectedSystem({ id: systemId, color: systemColor });
    setSelectedLocation(null);
    setSelectedRows([]);
  };

  const handleLocationSelect = (locationId) => {
    setSelectedLocation(locationId);
    setSelectedRows([]);
  };

  // Define a function to add charges to the selectedRows
  const handleAddToChargeSheet = (charge) => {
    setSelectedRows((prevSelectedRows) => {
      // Add charge to the list if it's not already present
      if (!prevSelectedRows.some((row) => row.ChargeID === charge.ChargeID)) {
        return [...prevSelectedRows, charge];
      }
      return prevSelectedRows;
    });
  };

  return (
    <div className="min-h-screen bg-[#00e3d2] p-4 charge-master-page">
      <div className="max-w-7xl mx-auto bg-gray-900 border border-gray-700 rounded-lg shadow-lg p-6 text-white">
        <header className="text-4xl font-bold mb-6">ChargeMaster</header>
        <div className="state-transition">
          <SystemList onSystemSelect={handleSystemSelect} />
        </div>
        {selectedSystem.id && (
          <div className="state-transition">
            <LocationList
              selectedSystem={selectedSystem}
              onLocationSelect={handleLocationSelect}
            />
          </div>
        )}
        {selectedLocation && (
          <div className="state-transition">
            <div className="location-details-container-center">
              <LocationDetails locationId={selectedLocation} />
            </div>
          </div>
        )}
        <ChargeTableDisplay
          systemId={selectedSystem.id}
          locationId={selectedLocation}
          handleAddToChargeSheet={handleAddToChargeSheet}
        />
      </div>
    </div>
  );
}

export default ChargeMasterPage;
