import React, { useEffect, useState } from "react";
import { useChargeSheet } from "../contexts/ChargeSheetContext";

const ActiveChargeSheet = () => {
  const { selectedChargeSheet } = useChargeSheet();
  const [chargeSheetName, setChargeSheetName] = useState("");

  useEffect(() => {
    if (selectedChargeSheet) {
      setChargeSheetName(selectedChargeSheet.ChargeSheetNameDefault);
    } else {
      setChargeSheetName("");
    }
  }, [selectedChargeSheet]);

  return (
    <div className="active-charge-sheet">
      {chargeSheetName ? (
        <p>Active Charge Sheet: {chargeSheetName}</p>
      ) : (
        <p>No active charge sheet selected.</p>
      )}
    </div>
  );
};

export default ActiveChargeSheet;
