import React, { useState } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useUser } from "../contexts/UserContext"; // Adjust the import path as necessary

const LoginPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const { login } = useUser();
  const navigate = useNavigate();

  const handleEmailChange = (e) => setEmail(e.target.value);
  const handlePasswordChange = (e) => setPassword(e.target.value);

  const handleLogin = async () => {
    const loginData = { email, password };

    try {
      // Attempt to authenticate the user
      console.log("Attempting to authenticate user:", email);
      const loginResponse = await fetch(
        "https://smithtech.io/auth/authenticate",
        {
          method: "POST",
          headers: { "Content-Type": "application/json" },
          body: JSON.stringify(loginData),
        }
      );

      if (!loginResponse.ok) {
        console.error(`Login failed with status: ${loginResponse.status}`);
        throw new Error("Login failed");
      }

      const loginResult = await loginResponse.json();

      // Check for JWT token in the response
      if (!loginResult.access_token) {
        throw new Error("Authentication token missing in response");
      }

      // Fetch user details using the token
      console.log("Fetching user details");
      const userResponse = await fetch("https://smithtech.io/auth/user", {
        method: "GET",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Bearer ${loginResult.access_token}`,
        },
      });

      if (!userResponse.ok) {
        console.error(
          `Failed to fetch user data with status: ${userResponse.status}`
        );
        throw new Error("Failed to fetch user data");
      }

      const userData = await userResponse.json();
      console.log("Retrieved user data:", userData);

      // Update user context
      login(userData);
      console.log("User logged in with data:", userData);

      // Navigate to the dashboard after login
      navigate("/dashboard");
    } catch (error) {
      console.error("Login error:", error.message);
      alert(error.message);
    }
  };

  return (
    <div className="min-h-screen flex items-center justify-center bg-[#00e3d2]">
      <div className="login-container max-w-md mx-auto p-6 bg-gray-800 text-white rounded-lg shadow-lg">
        <h2 className="text-3xl font-bold mb-4">Login</h2>
        <div className="form-group mb-4">
          <label className="block mb-2">Email:</label>
          <input
            type="email"
            placeholder="Enter your email"
            value={email}
            onChange={handleEmailChange}
            className="w-full p-2 rounded bg-gray-700 text-white"
          />
        </div>
        <div className="form-group mb-6">
          <label className="block mb-2">Password:</label>
          <input
            type="password"
            placeholder="Enter your password"
            value={password}
            onChange={handlePasswordChange}
            className="w-full p-2 rounded bg-gray-700 text-white"
          />
        </div>
        <button
          type="button"
          onClick={handleLogin}
          className="w-full bg-[#00c2c0] text-white py-2 px-4 rounded hover:bg-[#00e3d2] font-bold"
        >
          Login
        </button>
        <p className="mt-4">
          Don't have an account?{" "}
          <Link to="/signup" className="text-[#00c2c0] hover:text-[#00e3d2]">
            Sign Up
          </Link>
        </p>
      </div>
    </div>
  );
};

export default LoginPage;
