import React, { useContext } from "react";
import axios from "axios";
import { useUser } from "../../contexts/UserContext"; // Adjust the import path as necessary

const ChargeSheetDelete = ({ userChargeSheetId, onDeleteSuccess }) => {
  const { user } = useUser();

  const handleDelete = async () => {
    if (!user || !user.UserId) {
      alert("User ID is not available.");
      return;
    }

    const deleteUrl = `https://smithtech.io/react/chargesheet/delete/${user.UserId}/${userChargeSheetId}`;
    console.log("Deleting charge sheet with query:", deleteUrl); // Log the delete query

    try {
      const response = await axios.delete(deleteUrl);
      if (response.status === 200) {
        onDeleteSuccess();
        alert("Charge sheet deleted successfully.");
      }
    } catch (error) {
      console.error("Error deleting charge sheet", error);
      alert("Could not delete charge sheet.");
    }
  };

  return (
    <button
      onClick={handleDelete}
      className="bg-red-500 text-white p-2 rounded hover:bg-salmon-600"
    >
      Delete Charge Sheet
    </button>
  );
};

export default ChargeSheetDelete;
