import React, { useState, useEffect } from "react";
import axios from "axios";
import { useUser } from "../../contexts/UserContext";
import { useChargeSheet } from "../../contexts/ChargeSheetContext"; // Import useChargeSheet
import ChargeSheetDetails from "./ChargeSheetDetails";
import Select from "react-select";

const ChargeSheetComponent = () => {
  const { user } = useUser();
  const { setSelectedChargeSheet } = useChargeSheet(); // Use the context
  const [chargeSheets, setChargeSheets] = useState([]);
  const [currentPage, setCurrentPage] = useState(1);
  const [selectedSheet, setSelectedSheet] = useState(null);
  const [newChargeSheetName, setNewChargeSheetName] = useState("");
  const [showPopup, setShowPopup] = useState(false);
  const [popupMessage, setPopupMessage] = useState("");

  const itemsPerPage = 10;

  useEffect(() => {
    if (user && user.UserId) {
      const url = `https://smithtech.io/react/chargesheet/list/${user.UserId}`;
      axios
        .get(url)
        .then((response) => {
          const sortedSheets = response.data.charge_sheets.sort(
            (a, b) => new Date(b.CreatedAt) - new Date(a.CreatedAt)
          );
          setChargeSheets(sortedSheets);
        })
        .catch((error) => {
          console.error("Error fetching charge sheets", error);
        });
    }
  }, [user]);

  const formatDate = (dateString) => {
    const options = { year: "numeric", month: "long", day: "numeric" };
    return new Date(dateString).toLocaleDateString(undefined, options);
  };

  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  const handleSelectChange = (selectedOption) => {
    setSelectedSheet(selectedOption);
    if (selectedOption) {
      const selectedChargeSheet = chargeSheets.find(
        (sheet) => sheet.UserChargeSheetID === selectedOption.value
      );
      setSelectedChargeSheet(selectedChargeSheet);
      setPopupMessage(
        `${selectedChargeSheet.ChargeSheetNameDefault} is now active`
      );
      setShowPopup(true);
    } else {
      setSelectedChargeSheet(null);
      setShowPopup(false);
    }
  };

  const selectOptions = chargeSheets.map((sheet) => ({
    value: sheet.UserChargeSheetID,
    label: sheet.ChargeSheetNameDefault,
  }));

  const filteredSheets = selectedSheet
    ? chargeSheets.filter(
        (sheet) => sheet.UserChargeSheetID === selectedSheet.value
      )
    : chargeSheets.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
      );

  const onDeleteSuccess = (deletedSheetId) => {
    setChargeSheets((currentSheets) => {
      const updatedSheets = currentSheets.filter(
        (sheet) => sheet.UserChargeSheetID !== deletedSheetId
      );
      console.log(updatedSheets); // Log to check if the state is updated correctly
      return updatedSheets;
    });
  };

  const createChargeSheet = async () => {
    if (!user || !user.UserId) {
      alert("User ID is not available.");
      return;
    }

    const payload = {
      user_id: user.UserId,
      charge_sheet_name_default: newChargeSheetName,
    };

    try {
      const response = await axios.post(
        "https://smithtech.io/react/chargesheet/create",
        payload
      );
      if (response.status === 200 && response.data.charge_sheet_id) {
        alert(
          `Charge Sheet created successfully with ID: ${response.data.charge_sheet_id}`
        );

        // Create a new charge sheet object
        const newChargeSheet = {
          UserChargeSheetID: response.data.charge_sheet_id,
          ChargeSheetNameDefault: newChargeSheetName,
          CreatedAt: new Date().toISOString(), // Or format the date as needed
          // Include any other relevant fields
        };

        // Update the chargeSheets state
        setChargeSheets((currentSheets) => [newChargeSheet, ...currentSheets]);

        // Reset the form
        setNewChargeSheetName("");
      } else {
        alert("Failed to create charge sheet.");
      }
    } catch (error) {
      console.error("Error creating charge sheet", error);
      alert("An error occurred while creating the charge sheet.");
    }
  };

  const Popup = ({ message, onClose }) => (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center">
      <div className="bg-white p-4 rounded">
        <p>{message}</p>
        <button
          onClick={onClose}
          className="mt-2 px-4 py-2 bg-blue-500 text-white rounded"
        >
          Close
        </button>
      </div>
    </div>
  );

  return (
    <div className="min-h-screen p-4">
      {showPopup && (
        <Popup message={popupMessage} onClose={() => setShowPopup(false)} />
      )}
      <div className="max-w-7xl mx-auto bg-gray-900 border border-gray-700 rounded-lg shadow-lg p-6 text-white">
        <header className="text-4xl font-bold mb-6">Your Charge Sheets</header>

        <div className="flex gap-4 mb-4">
          <input
            type="text"
            value={newChargeSheetName}
            onChange={(e) => setNewChargeSheetName(e.target.value)}
            placeholder="New Charge Sheet Name"
            className="px-3 py-2 bg-gray-800 text-white border border-gray-300 rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
          />
          <button
            onClick={createChargeSheet}
            className="bg-[#00e3d2] hover:bg-teal-700 text-white font-bold py-2 px-4 rounded"
          >
            Create Charge Sheet
          </button>
        </div>

        <Select
          options={selectOptions}
          onChange={handleSelectChange}
          placeholder="Search by Charge Sheet Name"
          className="text-black mb-4"
          styles={{
            control: (provided) => ({
              ...provided,
              backgroundColor: "rgb(31 41 55)", // Tailwind color 'bg-gray-800'
              color: "white",
            }),
            singleValue: (provided) => ({
              ...provided,
              color: "white",
            }),
            placeholder: (provided) => ({
              ...provided,
              color: "rgb(156 163 175)", // Tailwind color 'text-gray-400'
            }),
          }}
        />

        {filteredSheets.length > 0 ? (
          filteredSheets.map((sheet) => (
            <div
              key={sheet.UserChargeSheetID}
              className="bg-gray-800 p-4 rounded-lg shadow m-2"
            >
              <div className="flex justify-between items-center">
                <h3 className="text-xl font-bold">
                  {sheet.ChargeSheetNameDefault}
                </h3>
                <span className="text-sm text-gray-400">
                  {formatDate(sheet.CreatedAt)}
                </span>
              </div>
              <ChargeSheetDetails
                userId={user.UserId}
                chargeSheetId={sheet.UserChargeSheetID}
                onDeleteSuccess={() => onDeleteSuccess(sheet.UserChargeSheetID)}
              />
            </div>
          ))
        ) : (
          <p className="text-gray-500">No charge sheets available.</p>
        )}

        {/* Pagination Component Here */}
      </div>
    </div>
  );
};

export default ChargeSheetComponent;
