import React, { useState } from "react";
import axios from "axios";

function DescriptionSearch({ onResults }) {
  const [description, setDescription] = useState("");
  const [isLoading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const handleSearch = () => {
    if (!description.trim()) return; // Prevent search if input is empty
    setLoading(true);
    axios
      .get(`https://smithtech.io/react/charges/description/${description}`)
      .then((response) => {
        onResults(response.data);
        setLoading(false);
      })
      .catch((error) => {
        setError(error.message);
        setLoading(false);
      });
  };

  return (
    <div className="description-search-container max-w-lg mx-auto p-4">
      <input
        className="description-search-input bg-gray-700 text-white rounded-lg shadow-sm px-4 py-3 w-full mb-4 focus:outline-none focus:ring-2 focus:ring-teal-500 focus:border-transparent"
        type="text"
        value={description}
        onChange={(e) => setDescription(e.target.value)}
        placeholder="Enter Description"
      />
      <button
        className={`description-search-button bg-teal-700 hover:bg-teal-600 text-white px-6 py-3 rounded-lg transition duration-200 ${
          !description.trim() ? "opacity-50 cursor-not-allowed" : ""
        }`}
        onClick={handleSearch}
        disabled={!description.trim()}
      >
        Search Description
      </button>
      {isLoading && (
        <div className="loading-message text-white mt-3">Loading...</div>
      )}
      {error && (
        <div className="error-message text-red-500 mt-3">Error: {error}</div>
      )}
    </div>
  );
}

export default DescriptionSearch;
