import React from "react";
import "./Dashboard.css"; // Import the CSS for animations and additional styling

const Dashboard = () => {
  return (
    <div className="dashboard-container min-h-screen bg-[#00e3d2]">
      <div className="content-wrapper p-4">
        <div className="dashboard-content max-w-7xl mx-auto bg-gray-900 border border-gray-700 rounded-lg shadow-lg p-6 text-white">
          <h1 className="title text-4xl font-bold mb-6">Dashboard</h1>
          <p className="welcome-message">
            Welcome to your Dashboard. This is your central hub for managing
            charge sheets and other important information.
          </p>
          {/* Additional sections like recent activities, stats, etc. */}
        </div>
      </div>
    </div>
  );
};

export default Dashboard;
