import React, { useState, useEffect } from "react";
import axios from "axios";
import { useChargeSheet } from "../../contexts/ChargeSheetContext";

const ChargeSheetSelectActive = ({
  chargeSheet,
  onUpdateActiveChargeSheet,
}) => {
  const [message, setMessage] = useState("");
  const { setSelectedChargeSheet } = useChargeSheet();
  const [chargeSheetNameDefault, setChargeSheetNameDefault] = useState("");

  useEffect(() => {
    const fetchChargeSheetNameDefault = async () => {
      if (chargeSheet && chargeSheet.UserChargeSheetID) {
        try {
          const response = await axios.get(
            `https://smithtech.io/react/chargesheet/get_charge_sheet_data/${chargeSheet.UserID}/${chargeSheet.UserChargeSheetID}`
          );
          if (response.data && response.data[0]) {
            setChargeSheetNameDefault(response.data[0].ChargeSheetNameDefault);
          }
        } catch (error) {
          console.error("Error fetching ChargeSheetNameDefault", error);
        }
      }
    };

    // Call the API when chargeSheet changes
    fetchChargeSheetNameDefault();
  }, [chargeSheet]);

  const handleSelectChargeSheet = () => {
    if (!chargeSheet || Object.keys(chargeSheet).length === 0) {
      console.error("No charge sheet data available to set as active.");
      return;
    }

    setSelectedChargeSheet(chargeSheet);

    if (chargeSheetNameDefault.trim() !== "") {
      setMessage(`${chargeSheetNameDefault} set as active successfully!`);
      console.log("Active Charge Sheet Set:", chargeSheet);
      onUpdateActiveChargeSheet(chargeSheetNameDefault);

      // Clear the message after 3 seconds (adjust the duration as needed)
      setTimeout(() => {
        setMessage("");
      }, 3000);
    } else {
      setMessage("Charge sheet set as active successfully!");
      console.log("Active Charge Sheet Set:", chargeSheet);
      onUpdateActiveChargeSheet("Unknown");
    }
  };

  return (
    <div>
      <button
        onClick={handleSelectChargeSheet}
        className="mb-6 p-2 w-full text-lg rounded bg-teal-500 hover:bg-teal-600 transition duration-300"
      >
        Set as Active ChargeSheet
      </button>
      {message && <p className="text-green-500">{message}</p>}
    </div>
  );
};

export default ChargeSheetSelectActive;
