import React, { useState, useEffect } from "react";
import axios from "axios";
import Select from "react-select";
import InsurancePlanList from "./InsurancePlanList"; // Make sure this path is correct
import "./CarrierTextSearch.css";
function CarrierTextSearch() {
  const [carriers, setCarriers] = useState([]);
  const [selectedCarriers, setSelectedCarriers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  // Custom styling for Select component
  const customSelectStyles = {
    control: (styles) => ({
      ...styles,
      backgroundColor: "rgba(31, 41, 55)", // Dark background
      color: "white", // Light text
    }),
    option: (styles, { isFocused, isSelected }) => ({
      ...styles,
      backgroundColor: isSelected
        ? "#00e3d2"
        : isFocused
        ? "rgba(55, 65, 81)"
        : "rgba(31, 41, 55)",
      color: "white",
      cursor: "pointer",
    }),
    // Add other styles as needed
  };

  useEffect(() => {
    setLoading(true);
    axios
      .get("https://smithtech.io/react/eligibility/carriers")
      .then((response) => {
        const transformedCarriers = response.data.map((carrier) => ({
          value: carrier.CarrierID,
          label: carrier.CarrierName,
        }));
        setCarriers(transformedCarriers);
        setLoading(false);
      })
      .catch((error) => {
        console.error("Error fetching carriers:", error);
        setError(error.message);
        setLoading(false);
      });
  }, []);

  const handleCarrierSelect = (selectedOptions) => {
    setSelectedCarriers(selectedOptions || []);
  };

  if (loading) return <div className="text-white">Loading...</div>;
  if (error) return <div className="text-red-500">{error}</div>;

  return (
    // Set the background of the component to be dark
    <div className="min-h-screen bg-gray-900 p-4 text-white">
      <div className="max-w-7xl mx-auto rounded-lg shadow-lg p-6">
        <h3 className="text-xl font-bold mb-3">Insurance Eligibility</h3>
        <Select
          isMulti
          name="carriers"
          options={carriers}
          value={selectedCarriers}
          onChange={handleCarrierSelect}
          styles={customSelectStyles}
          classNamePrefix="react-select"
        />
        <div className="selected-carriers mt-4">
          <h4 className="font-semibold mb-2">Selected Carriers</h4>
          {selectedCarriers.map((selectedCarrier) => (
            <div
              key={selectedCarrier.value}
              className="selected-carrier-item mb-4"
            >
              {selectedCarrier.label}
              <InsurancePlanList carrierId={selectedCarrier.value} />
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}

export default CarrierTextSearch;
