import React from "react";
import { Link } from "react-router-dom";
import LandingPageCard from "./LandingPageCard"; // Ensure this path is correct
import "./LandingPage.css"; // Make sure the path is correct

const LandingPage = () => {
  const features = [
    {
      title: "ChargeMaster Insights",
      description:
        "Dive into a detailed exploration of charge data, uncovering pricing structures and billing practices across diverse healthcare systems. Gain invaluable insights for more informed healthcare financial decisions.",
    },
    {
      title: "Insurance Eligibility",
      description:
        "Effortlessly navigate through the complexities of insurance coverage. Our tool simplifies the process of verifying eligibility and understanding the nuances of various healthcare plans.",
    },
    {
      title: "Intuitive Data Analysis",
      description:
        "Harness the power of user-friendly analytics tools designed for healthcare professionals. Transform complex datasets into actionable insights, enabling better decision-making in healthcare management.",
    },
    {
      title: "Accessible Healthcare Information",
      description:
        "Bridge the gap in healthcare knowledge with our platform. We make complex healthcare data understandable and accessible, empowering both professionals and patients in the healthcare journey",
    },
    {
      title: "ChargeMasterGPT",
      description:
        "Using the OpenAI GPT technology, effortlessly retrieve elgibility and charge information for any healthcare plan. Our AI-powered tool can help you make informed healthcare decisions.",
    },
    // Add more features as needed
  ];

  return (
    <div className="landing-page bg-[#00e3d2] min-h-screen flex flex-col items-center justify-center">
      <div className="overlay-content text-center p-6 max-w-2xl mx-auto bg-gray-800 rounded-lg shadow-md">
        <h1 className="text-5xl font-bold mb-6 text-white">
          Empowering Healthcare Decisions
        </h1>
        <p className="text-gray-200 mb-4">
          SmithTech Solutions Healthcare Insights brings clarity and
          transparency to healthcare data.
        </p>
        <div className="feature-list grid grid-cols-1 md:grid-cols-2 gap-4 mb-6">
          {features.map((feature, index) => (
            <LandingPageCard
              key={index}
              title={feature.title}
              description={feature.description}
            />
          ))}
        </div>
        <div className="call-to-action">
          <Link
            to="/login"
            className="inline-block bg-teal-700 hover:bg-teal-600 text-white font-semibold px-6 py-2 rounded-lg mr-2"
          >
            Login
          </Link>
          <Link
            to="/signup"
            className="inline-block bg-teal-700 hover:bg-teal-600 text-white font-semibold px-6 py-2 rounded-lg"
          >
            Sign Up
          </Link>
        </div>
      </div>
    </div>
  );
};

export default LandingPage;
