import React, { useEffect, useState } from "react";
import axios from "axios";
import Select from "react-select";

function ColumnSelector({ systemId, selectedColumns, setSelectedColumns }) {
  const [availableColumns, setAvailableColumns] = useState([]);

  useEffect(() => {
    if (systemId) {
      axios
        .get(`https://smithtech.io/react/columns/${systemId}`)
        .then((response) => {
          const columns = response.data.columns.map((column) => ({
            value: column,
            label: column,
          }));
          setAvailableColumns(columns);
        })
        .catch((error) => console.error(`Error fetching columns: ${error}`));
    }
  }, [systemId]);

  const handleChange = (selectedOptions) => {
    const selectedColumnNames = selectedOptions.map((option) => option.value);
    setSelectedColumns(selectedColumnNames);
  };

  const selectedValues = selectedColumns.map((column) => ({
    value: column,
    label: column,
  }));

  // Define custom styles for the Select component
  const customStyles = {
    container: (provided) => ({
      ...provided,
      width: "100%",
    }),
    control: (provided) => ({
      ...provided,
      backgroundColor: "rgba(31, 41, 55)", // Dark background
      color: "white", // Light text
    }),
    option: (provided, state) => ({
      ...provided,
      backgroundColor: state.isSelected ? "#00e3d2" : "rgba(31, 41, 55)", // Teal for selected, dark for others
      color: "white",
      cursor: "pointer",
    }),
    multiValue: (provided) => ({
      ...provided,
      backgroundColor: "rgba(55, 65, 81)", // Slightly lighter background for tags
    }),
    multiValueLabel: (provided) => ({
      ...provided,
      color: "white", // Light text for tag labels
    }),
  };

  return (
    <div className="column-selector bg-gray-900 p-4 rounded-lg shadow text-white">
      <Select
        isMulti
        name="columns"
        options={availableColumns}
        value={selectedValues}
        onChange={handleChange}
        styles={customStyles} // Apply custom styles
      />
    </div>
  );
}

export default ColumnSelector;
