import React, { useContext } from "react";
import { useChargeSheet } from "../contexts/ChargeSheetContext";

function ResultItem({ result, orderedKeys, isValidDetail }) {
  const { selectedChargeSheet } = useChargeSheet();
  const { UserChargeSheetID, UserID } = selectedChargeSheet || {};

  const shouldDisplayKey = (key) => {
    return isValidDetail(key, result[key]) && !key.includes("ID");
  };

  const otherKeys = Object.keys(result)
    .filter((key) => !orderedKeys.includes(key) && shouldDisplayKey(key))
    .sort();

  const handleAddToChargeSheet = async () => {
    if (!UserChargeSheetID || !UserID) {
      console.error("UserChargeSheetID or UserID not available in context.");
      return;
    }

    const data = {
      charge_sheet_id: UserChargeSheetID,
      user_id: UserID,
      location_id: result.LocationID,
      system_id: result.SystemID,
      charge_id: result.ChargeID,
    };

    try {
      // Send a POST request to the API to add the ChargeSheetDetail (replace with your API endpoint)
      const response = await fetch(
        "https://smithtech.io/react/chargesheet/add_charge",
        {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(data),
        }
      );

      if (response.ok) {
        console.log("Charge added successfully");
      } else {
        console.error("Failed to add charge:", response.statusText);
      }
    } catch (error) {
      console.error("Error adding charge:", error);
    }
  };

  return (
    <div className="bg-gray-800 p-4 mb-4 rounded-lg shadow">
      {orderedKeys.concat(otherKeys).map(
        (key) =>
          shouldDisplayKey(key) && (
            <div key={key} className="text-white py-1">
              <strong className="font-semibold">
                {key.replace(/([A-Z])/g, " $1").trim()}:
              </strong>
              <span className="ml-2">{result[key]}</span>
            </div>
          )
      )}
      <button
        className="bg-teal-500 hover:bg-teal-600 text-white font-semibold py-2 px-4 rounded mt-4"
        onClick={handleAddToChargeSheet}
      >
        Add to Charge Sheet
      </button>
    </div>
  );
}

export default ResultItem;
