import TableRow from "./TableRow";
import React from "react";
import "./Table.css";
function Table({ charges, selectedColumns, handleAddToChargeSheet }) {
  // Utility function to format the column header keys
  function formatKey(key) {
    return key
      .replace(/_/g, " ")
      .replace(/([A-Z])(\d)/g, "$1 $2")
      .trim();
  }

  return (
    <div className="overflow-x-auto">
      <table className="min-w-full bg-gray-800 text-white rounded-lg shadow-lg">
        <thead className="bg-gray-700">
          <tr>
            <th className="px-4 py-2">Action</th>
            {selectedColumns.map((column, index) => (
              <th key={index} className="px-4 py-2">
                {formatKey(column)}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {charges.length > 0 ? (
            charges.map((charge, rowIndex) => (
              <TableRow
                key={rowIndex}
                charge={charge}
                selectedColumns={selectedColumns}
                handleAddToChargeSheet={handleAddToChargeSheet}
              />
            ))
          ) : (
            <tr>
              <td
                colSpan={selectedColumns.length + 1}
                className="text-center py-4"
              >
                No charges data available.
              </td>
            </tr>
          )}
        </tbody>
      </table>
    </div>
  );
}

export default Table;
